import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import IMAGE from "../../assets/images/logo-web.png";
import { PrimaryFontSize, TextMainColor } from "../../consts/Consts";

//STYLED-COMPONENTS

const HeaderContainer = styled.header`
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: auto;
  box-shadow: 0px 0px 4px 2px black;
  @media (max-width: 700px) {
    justify-content: space-space-evenly;
  }
`;
//Logo
const Logo = styled.div`
  display: flex;
  align-items: center;
`;
const MainName = styled.h1`
  font-size: ${PrimaryFontSize};
  font-style: italic;
  font-weight: 500;
  color: ${TextMainColor};

  @media (max-width: 700px) {
    font-size: 20px;
  }
`;
const Image = styled.img`
  width: 4vw;
  margin-right: 1.3em;
  @media(max-width: 700px) {
    width:9vw;
  }
`;
//Nav
const Nav = styled.nav`
  display: flex;
  align-items: center;
  font-size: 1.2em;
  @media(max-width: 700px) {
    font-size:1em;
}
`;

const linkStyles = {
  color: TextMainColor,
  fontWeight: "500",
  transition: "0.4s color ease-in-out",
  marginLeft: "1.4em",
  textDecoration: "none",

};
const Header = ({initLink,listOfOwnerLink}) => {
  return (
    <HeaderContainer>
      <Logo>
        <Image src={IMAGE}></Image>
        <MainName>Clínica de mascotas</MainName>
      </Logo>
      <Nav>
        <Link className={initLink} style={linkStyles} to="/">
          Inicio
        </Link>
        <Link className={listOfOwnerLink} style={linkStyles} to="/list-of-owners">
          Lista de propietarios
        </Link>
      </Nav>
    </HeaderContainer>
  );
};

export default Header;
