import React from 'react'
import styled from 'styled-components'
  
import { SecondaryColor,TextSecondaryColor } from '../../consts/Consts'


const ButtonFixedElement = styled.div`
    position: fixed;
    z-index:2;
    top:90% ;
    left:90%;
    transform:translate(-50%, -50%);
    clip-path: polygon(30% 0%, 70% 0%, 100% 30%, 100% 70%, 70% 100%, 30% 100%, 0% 70%, 0% 30%);

    width:50px;
    height:50px;
    display:flex;
    border-radius:25%;
    align-items:center;
    justify-content:center;
    box-shadow:0 0 2px 2px black;
    background-color:${SecondaryColor};
    cursor:pointer;

`
const Icon = styled.i`
    font-size:18px;
    padding-left:2px;
    color:${TextSecondaryColor};
    text-align:center;

`


 const ButtonFixed = ({onClick} ) => {

    
    return (
        <ButtonFixedElement onClick={onClick}>
          <Icon className="fas fa-user-plus"></Icon>
        </ButtonFixedElement>
    )
}

export default ButtonFixed
