import React from "react";
import styled from "styled-components";
import {
  Thead,
  Row,
  Column,
} from "../../CommonStyledComponents/StyledComponents";
import { TextMainColor } from "../../consts/Consts";

const TableContainer = styled.div`
  width: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
`;
const TableElement = styled.table`
  border-collapse: collapse;
  text-align: center;
  width: 70%;
  margin: auto;

  @media (max-width: 700px) {
    width:100%;
  }  
`;
const NameOfTable = styled.h1`
  font-weight: 500;
  font-size: 30px;

  
  text-align: center;
  color: ${TextMainColor};
  @media (max-width: 700px) {
    font-size: 25px;
  }
`;
const ScrollContainer = styled.section`
  @media (max-width: 700px) {
    overflow-x: scroll;
    overflow-y:hidden;
  }  

`;
const Table = ({ children, nameOfTable, columnTitles, list }) => {
  return (
    <div>
      {list < 1 ? (
        <NameOfTable>No hay propietarios agregados</NameOfTable>
      ) : (
        <TableContainer>
          <NameOfTable>{nameOfTable}</NameOfTable>
          <ScrollContainer>
            <TableElement>
              <Thead>
                <Row>
                  {columnTitles.map((e) => (
                    <Column key={e}>{e}</Column>
                  ))}
                </Row>
              </Thead>

              {children}
            </TableElement>
          </ScrollContainer>
        </TableContainer>
      )}
    </div>
  );
};

export default Table;
