import React from 'react'
import styled from 'styled-components';
import { Button } from '../../CommonStyledComponents/StyledComponents';
import { TextMainColor } from '../../consts/Consts';

const FormContainer = styled.div`
    width:95%;
    margin:auto;    

`
const FormTitle = styled.h1`
    font-size:25px;
    font-weight:500;
    text-align:center;
    color:${TextMainColor};

`
const FormElement = styled.form`
    
`
 const Form = ({children,formTitle,textButton,action}) => {
    return (
        <FormContainer >
            <FormTitle>
                {formTitle}
            </FormTitle>
            <FormElement method="POST" onSubmit={action}>
                {
                    children
                }
                <Button >
                    {textButton}
                </Button>
            </FormElement>
            
            
        </FormContainer>
    )
}

export default Form;
