

import styled from "styled-components";
import {
    PrimaryColor,
    SecondaryColor,
    TextMainColor,
    TextSecondaryColor,
  } from "../consts/Consts";
  
const Thead = styled.thead`
  font-size: 20px;
  background-color: ${SecondaryColor};
  color: ${TextSecondaryColor};
  font-weight:500;
  @media (max-width: 700px) {
    font-size:18px;
  }  
`;
const Row = styled.tr`
  border-bottom: 1px solid ${PrimaryColor};
`;

const Tbody = styled.tbody`
  font-size:18px;
  @media (max-width: 700px) {
    font-size:16px;
  }  
`
const Column = styled.td`
  padding: 10px;
`;
const Button = styled.button`
  background-color: ${SecondaryColor};
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  margin: auto;
  font-size: 20px;
  padding: 7px 10%;
  outline: none;
  border: none;
  border-radius: 10px;
  color: ${TextSecondaryColor};
  cursor: pointer;
  transition: 0.5s opacity ease;
  margin:10px auto;

  &:hover {
    opacity: 0.95;
  }
`;
const IconButton = styled.i`
  cursor:pointer;
  color:${TextMainColor};
  font-size: 20px;
  @media (max-width: 700px) {
    font-size:22px;
  }  

`
export {
    Thead,Row,Column,Tbody,Button,IconButton
}