import React from "react";
import styled, { keyframes } from "styled-components";
import { SecondaryColor } from "../../consts/Consts";

const fadeIn = keyframes`
    0%{
        opacity:0;
    }
    100%{
        opacity:1;
    }
`;

const slideIn = keyframes`
    0%{
        top:-90vh;
    }
    100%{
        top:0vh;
        
    }
`;
const ModalElement = styled.div`
  position: fixed;
  width: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 90;
  opacity: 1;
  animation: ${fadeIn} 0.5s;
`;
const ModalContainer = styled.div`
  background-color: white;
  width: 60%;
  padding: 10px;
  padding-top: 30px;
  min-width: 30vw;
  max-width: 40vw;
  min-height: 50vh;
  max-height: 90vh;
  position: relative;
  top: 0vh;
  box-shadow: 0px 0px 5px 2px ${SecondaryColor};
  border-radius: 10px;
  animation: ${slideIn} 0.7s;
  @media (max-width: 700px) {
    min-width:90%;
  }
`;

const CloseModal = styled.div`
  position: absolute;
  right: 2em;
  top: 1em;
  cursor: pointer;
`;
const CloseIcon = styled.i`
  font-size: 25px;
  color: ${SecondaryColor};
`;
const Modal = ({ children, closeModal, reference }) => {
  return (
    <ModalElement ref={reference}>
      <ModalContainer>
        <CloseModal onClick={closeModal}>
          <CloseIcon className="fas fa-times" />
        </CloseModal>
        {children}
      </ModalContainer>
    </ModalElement>
  );
};
export default Modal;
