import "./App.css";
import {BrowserRouter as Router, Route, Switch} from "react-router-dom"
import {Home,ListOfOwners,NotFound} from "./Routes/Routes"

function App() {

  return (
          <Router>
            <Switch>
              <Route path="/" exact sensitive component={Home}></Route>
              <Route path="/list-of-owners" exact sensitive component={ListOfOwners}></Route>
              <Route component={NotFound}></Route>
            </Switch>
          </Router>
          
  );
}

export default App;
