import React, { Fragment, useState, useRef } from "react";
import Header from "../components/layout/Header";
import Table from "../components/common/Table";
import { Tbody } from "../CommonStyledComponents/StyledComponents";
import Modal from "../components/common/Modal";
import Form from "../components/common/Form";
import FormInputs from "../components/common/FormInputs";
import ButtonFixed from "../components/layout/ButtonFixed";
import OwnerItem from "../components/common/OwnerItem";

const initialValuesOfOwner = {
  name: "",
  address: "",
  phone: "",
};
const ListOfOwners = () => {
  const [owners, setOwners] = useState([]);
  const [dataToEdit, setDataToEdit] = useState(null);
  const [formOwner, setFormOwner] = useState(initialValuesOfOwner);
  const refFormOwner = useRef();

  /* const refFormMascot = useRef();
  const refListOfMascots = useRef(); */
  const TextFieldsFormOwner = [
    {
      id: "name",
      name: "Nombre",
      type: "text",
      icon: "fas fa-user-tie",
      value: formOwner.name,
    },
    {
      id: "address",
      name: "Dirección",
      type: "text",
      icon: "fas fa-map-marker-alt",
      value: formOwner.address,
    },
    {
      id: "phone",
      name: "Teléfono",
      type: "number",
      icon: "fas fa-phone-alt",
      value: formOwner.phone,
    },
  ];
  const handleChange = (e) => {
    setFormOwner({ ...formOwner, [e.target.name]: e.target.value });
  };
  const addOwner = (e) => {
    e.preventDefault();
    if (dataToEdit === null) {
      setOwners([...owners, { ...formOwner, id: owners.length }]);
    } else {
      const updatedOwners = owners.map((o) => {
        if (o.id === formOwner.id) {
          console.log(o);
          o = formOwner;
        }
        return o;
      });
      console.log(updatedOwners);
      setOwners(updatedOwners);
      setDataToEdit(null);
    }
    setFormOwner(initialValuesOfOwner);
    closeModal(refFormOwner);
  };
  const editOwner = (owner) => {
    setDataToEdit(owner);
    openModal(refFormOwner);
    setFormOwner(owner);
  };
  const deleteOwner = (id) => {
    console.log(id);
    const updatedOwners = owners.filter((o) => o.id !== id);
    console.log(updatedOwners);
    setOwners(updatedOwners);
  };

  const openModal = (refModal) => {
    refModal.current.style.display = "flex";
  };

  const closeModal = (refModal) => {
    refModal.current.style.display = "none";
    setFormOwner(initialValuesOfOwner);
    setDataToEdit(null);
  };
  return (
    <Fragment>
      <Modal
        reference={refFormOwner}
        closeModal={() => closeModal(refFormOwner)}
      >
        <Form
          action={addOwner}
          formTitle={
            dataToEdit === null ? "Añade un propietario" : "Actualiza los datos"
          }
          textButton={dataToEdit === null ? "Agregar" : "Actualizar"}
        >
          <FormInputs
            formOwner={formOwner}
            handleChange={handleChange}
            textFieldsFormOwner={TextFieldsFormOwner}
          />
        </Form>
      </Modal>
      <Header listOfOwnerLink={"active"} />
      <Table
        nameOfTable={"Lista de propietarios"}
        columnTitles={[
          "ID",
          "Nombre",
          "Dirección",
          "Teléfono",
          "Mascotas",
          "Opciones",
        ]}
        list={owners}
      >
        <Tbody>
          {owners.map((e) => (
            <OwnerItem
              key={e.id}
              owner={e}
              editUser={editOwner}
              deleteUser={deleteOwner}
            />
          ))}
        </Tbody>
      </Table>
 
        <ButtonFixed onClick={() => openModal(refFormOwner)}></ButtonFixed>
  
    </Fragment>
  );
};
export default ListOfOwners;
