import React from "react";
import {
  Row,
  Column,
  IconButton,
} from "../../CommonStyledComponents/StyledComponents";
import styled from "styled-components";
import ToolTip from "./ToolTip";

const IconButtonsContainer = styled.div`
  display: flex;
  justify-content: space-evenly;
  align-items: center;
`;
const OwnerItem = ({ owner, editUser, deleteUser }) => {
  return (
    <Row key={owner.id}>
      <Column>{owner.id + 1}</Column>
      <Column>{owner.name}</Column>
      <Column>{owner.address}</Column>
      <Column>{owner.phone}</Column>
      <Column>
        <IconButtonsContainer>
          <ToolTip text={"mascotas de "+owner.name}>
            <IconButton className="fas fa-eye" />
          </ToolTip>
          <ToolTip text={"Agrega una mascota"}>
            <IconButton className="fas fa-plus-circle" />
          </ToolTip>
        </IconButtonsContainer>
      </Column>
      <Column>
        <IconButtonsContainer>
          <ToolTip text={"Edita un propietario"}>
            <IconButton
              onClick={() => {
                editUser(owner);
              }}
              className="fas fa-user-edit"
            />
          </ToolTip>
          <ToolTip text={"Elimina un propietario"}>
            <IconButton
              onClick={() => {
                deleteUser(owner.id);
              }}
              className="fas fa-user-times"
            />
          </ToolTip>
        </IconButtonsContainer>
      </Column>
    </Row>
  );
};

export default OwnerItem;
