import React from "react";
import styled from "styled-components";
import { SecondaryColor, TextMainColor } from "../../consts/Consts";

const FormInputsContainer = styled.div``;

const TextFieldContainer = styled.div`
  width: 70%;
  margin: auto;
  @media (max-width: 700px) {
    width:90%;
  }  
`;

const Icon = styled.i`
  font-size: 20px;
  color: ${SecondaryColor};
  display: flex;
  align-items: center;
`;

const Input = styled.input`
  border: none;
  outline: none;
  width: 100%;
  padding-left: 10px;
  font-size: 19px;
  height: 20px;
`;
const InputContainer = styled.div`
  width: 100%;
  border: 2px solid ${TextMainColor};
  display: flex;
  padding: 5px;
  margin: 5px auto;

`;
const Label = styled.label`
  font-size: 20px;
  color: ${TextMainColor};
  font-weight: 500;
`;

const FormInputs = ({ textFieldsFormOwner, handleChange }) => {
  return (
    <FormInputsContainer>
      {textFieldsFormOwner.map((e) => (
        <TextFieldContainer key={e.id}>
          <Label htmlFor={e.id}>{e.name}</Label>
          <InputContainer>
            <Icon className={e.icon} />
            <Input title="a" required value={e.value} name={e.id} onChange={handleChange} type={e.type} id={e.id} />
          </InputContainer>
        </TextFieldContainer>
      ))}
    </FormInputsContainer>
  );
};
export default FormInputs;
